*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content{
  height: 100vh;
}

.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: rgb(26, 25, 25);
  
}

/*Trasection page*/
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px grey;
}

/* antd icons */
 .anticon svg{
  font-size: 20px;
  cursor: pointer;
 }

 .active-icon{
  color: black;
 }

 .inactive-icon{
  color: white;
 }

 .switch-icon{
  border: 1px solid 15px black;
  border-radius: 5px;
  padding: 10px 15px ;
 }

 .content{
  background: rgb(41, 40, 40);  
 }

 .filters{
  background: #f25151;
 }

 .FOOTER{
  background: rgb(238,165,141);
  background: linear-gradient(90deg, rgba(238,165,141,1) 0%, rgba(251,99,26,1) 41%, rgba(242,81,81,1) 100%);
 }

 .navigation{
  background: rgb(238,165,141);
  background: linear-gradient(90deg, rgba(238,165,141,1) 0%, rgba(251,99,26,1) 41%, rgba(242,81,81,1) 100%);
  padding: 15px;
 }

 .card{
  background-color:  rgb(247, 226, 226);
 }

 h1{
  text-align: center;
  padding: 10px;
 }
 h4{
  color: white;
 }
 
 .navbar-brand{
  color: rgb(33, 30, 30);
 }

 .loginb{
  padding-left: 80px;
 }
 .regb{
  padding-left: 90px;
 }

 .loginbox{
  background: rgb(238,165,141);
  background: linear-gradient(90deg, rgba(238,165,141,1) 0%, rgba(251,99,26,1) 41%, rgba(242,81,81,1) 100%);
  padding: 40px;
  border-radius: 20px;
 }
.linkr{
  color: black;
}
 


